.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #2c3e50;
  color: #ecf0f1;
  font-family: 'Arial', sans-serif;
}

h1 {
  margin-bottom: 40px;
  font-size: 2.5rem;
  text-align: center;
  display: flex;
  justify-content: center;
  gap: 5px;
}

h1 span {
  display: inline-block;
  background-color: #ecf0f1; /* Light background for the blocks */
  color: #2c3e50; /* Dark color for the text */
  padding: 10px;
  border-radius: 5px;
  font-weight: bold;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
}

.clock-container {
  position: relative;
  width: 350px; /* Increased size for better spacing */
  height: 350px;
  border: 4px solid #ecf0f1;
  border-radius: 50%;
  background-color: #34495e;
  display: flex;
  align-items: center;
  justify-content: center;
}

.number {
  position: absolute;
  font-size: 1.25rem;
  font-weight: bold;
  color: #ecf0f1;
}

.clock-hand {
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform-origin: bottom center;
}
